// Types
interface SalesRoom {
  id: string
  name: string
  company: string
  dealValue: number
  status: 'active' | 'draft' | 'closed' | 'archived'
  createdAt: string
  lastVisited: string | null
  totalViews: number
  uniqueVisitors: number
  avgTimeSpent: number // in seconds
  contentItems: number
  stakeholders: Stakeholder[]
  engagementScore: number
  slug: string
  thumbnail?: string
}

interface Stakeholder {
  id: string
  name: string
  email: string
  role: string
  company: string
  lastVisit: string | null
  totalViews: number
  timeSpent: number
  isChampion: boolean
}

interface ContentItem {
  id: string
  name: string
  type: 'document' | 'video' | 'presentation' | 'image' | 'link' | 'proposal'
  fileSize?: string
  uploadedAt: string
  views: number
  avgViewTime: number
  downloads: number
  category: string
  thumbnail?: string
}

interface RoomTemplate {
  id: string
  name: string
  description: string
  category: string
  contentCount: number
  usageCount: number
  thumbnail?: string
  sections: string[]
}

interface EngagementEvent {
  id: string
  roomId: string
  roomName: string
  visitorName: string
  visitorEmail: string
  action: 'view' | 'download' | 'share' | 'comment' | 'time_spent'
  contentName?: string
  timestamp: string
  duration?: number
}

