// Helper Functions
const formatCurrency = (amount: number) => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
  }).format(amount)
}

const formatTime = (seconds: number) => {
  if (seconds < 60) return `${seconds}s`
  if (seconds < 3600) return `${Math.floor(seconds / 60)}m ${seconds % 60}s`
  return `${Math.floor(seconds / 3600)}h ${Math.floor((seconds % 3600) / 60)}m`
}

const getStatusColor = (status: string) => {
  switch (status) {
    case 'active': return 'bg-emerald-100 text-emerald-700'
    case 'draft': return 'bg-gray-100 text-gray-700'
    case 'closed': return 'bg-blue-100 text-blue-700'
    case 'archived': return 'bg-gray-100 text-gray-500'
    default: return 'bg-gray-100 text-gray-700'
  }
}

const getContentIcon = (type: string) => {
  switch (type) {
    case 'document': return 'üìÑ'
    case 'video': return 'üé¨'
    case 'presentation': return 'üìä'
    case 'image': return 'üñºÔ∏è'
    case 'link': return 'üîó'
    case 'proposal': return 'üìù'
    default: return 'üìÅ'
  }
}

const getEngagementColor = (score: number) => {
  if (score >= 80) return 'text-emerald-600'
  if (score >= 50) return 'text-amber-600'
  return 'text-red-600'
}

// Main Component
export default function DigitalSalesRoomsPage() {
  const [activeTab, setActiveTab] = useState('rooms')
  const [selectedRoom, setSelectedRoom] = useState<SalesRoom | null>(null)
  const [showRoomModal, setShowRoomModal] = useState(false)
  const [showCreateModal, setShowCreateModal] = useState(false)

  const metrics = useMemo(() => {
    const activeRooms = mockRooms.filter(r => r.status === 'active').length
    const totalViews = mockRooms.reduce((sum, r) => sum + r.totalViews, 0)
    const totalVisitors = mockRooms.reduce((sum, r) => sum + r.uniqueVisitors, 0)
    const avgEngagement = Math.round(mockRooms.filter(r => r.engagementScore > 0).reduce((sum, r) => sum + r.engagementScore, 0) / mockRooms.filter(r => r.engagementScore > 0).length)
    const totalDealValue = mockRooms.filter(r => r.status === 'active').reduce((sum, r) => sum + r.dealValue, 0)
    
    return { activeRooms, totalViews, totalVisitors, avgEngagement, totalDealValue }
  }, [])

  const handleViewRoom = (room: SalesRoom) => {
    setSelectedRoom(room)
    setShowRoomModal(true)
  }

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        <PageHeader
          title="Digital Sales Rooms"
          description="Create personalized buyer experiences and track engagement"
          actions={
            <div className="flex items-center gap-3">
              <button className="px-4 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50 text-sm flex items-center gap-2">
                <span>üìä</span>
                View Analytics
              </button>
              <button
                onClick={() => setShowCreateModal(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm flex items-center gap-2"
              >
                <span>+</span>
                Create Room
              </button>
            </div>
          }
        />

        {/* Tab Navigation */}
        <nav className="-mb-px flex space-x-6 overflow-x-auto" aria-label="Tabs">
          {SALES_ROOMS_TABS.map((tab) => {
            const isActive = tab.id === activeTab
            return (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`whitespace-nowrap border-b-2 py-4 px-1 text-sm font-medium transition-colors ${
                  isActive
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                }`}
              >
                {tab.label}
              </button>
            )
          })}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="px-4 sm:px-6 lg:px-8 pb-4 sm:pb-6 lg:pb-8">
        {activeTab === 'rooms' && (
          <RoomsTab
            rooms={mockRooms}
            metrics={metrics}
            events={mockEngagementEvents}
            onViewRoom={handleViewRoom}
            onCreateRoom={() => setShowCreateModal(true)}
          />
        )}
        {activeTab === 'content' && (
          <ContentLibraryTab content={mockContent} />
        )}
        {activeTab === 'templates' && (
          <TemplatesTab templates={mockTemplates} />
        )}
        {activeTab === 'analytics' && (
          <AnalyticsTab rooms={mockRooms} events={mockEngagementEvents} />
        )}
        {activeTab === 'settings' && (
          <SettingsTab />
        )}
      </div>

      {/* Room Detail Modal */}
      {showRoomModal && selectedRoom && (
        <RoomDetailModal
          room={selectedRoom}
          onClose={() => {
            setShowRoomModal(false)
            setSelectedRoom(null)
          }}
        />
      )}

      {/* Create Room Modal */}
      {showCreateModal && (
        <CreateRoomModal
          templates={mockTemplates}
          onClose={() => setShowCreateModal(false)}
        />
      )}
    </div>
  )
}

// ==============================================
// SALES ROOMS TAB
// ==============================================
function RoomsTab({
  rooms,
  metrics,
  events,
  onViewRoom,
  onCreateRoom,
}: {
  rooms: SalesRoom[]
  metrics: any
  events: EngagementEvent[]
  onViewRoom: (room: SalesRoom) => void
  onCreateRoom: () => void
}) {
  const [statusFilter, setStatusFilter] = useState<string>('')
  const [sortBy, setSortBy] = useState<'recent' | 'engagement' | 'value'>('recent')

  const filteredRooms = useMemo(() => {
    let result = [...rooms]
    if (statusFilter) {
      result = result.filter(r => r.status === statusFilter)
    }
    result.sort((a, b) => {
      if (sortBy === 'engagement') return b.engagementScore - a.engagementScore
      if (sortBy === 'value') return b.dealValue - a.dealValue
      return new Date(b.lastVisited || b.createdAt).getTime() - new Date(a.lastVisited || a.createdAt).getTime()
    })
    return result
  }, [rooms, statusFilter, sortBy])

  return (
    <div className="space-y-6">
      {/* KPI Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üö™</div>
          <div className="text-2xl font-bold text-gray-900">{metrics.activeRooms}</div>
          <div className="text-sm text-gray-500">Active Rooms</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üëÅÔ∏è</div>
          <div className="text-2xl font-bold text-gray-900">{metrics.totalViews}</div>
          <div className="text-sm text-gray-500">Total Views</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üë•</div>
          <div className="text-2xl font-bold text-gray-900">{metrics.totalVisitors}</div>
          <div className="text-sm text-gray-500">Unique Visitors</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üìà</div>
          <div className={`text-2xl font-bold ${getEngagementColor(metrics.avgEngagement)}`}>
            {metrics.avgEngagement}%
          </div>
          <div className="text-sm text-gray-500">Avg. Engagement</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üí∞</div>
          <div className="text-2xl font-bold text-gray-900">{formatCurrency(metrics.totalDealValue)}</div>
          <div className="text-sm text-gray-500">Pipeline Value</div>
        </div>
      </div>

      {/* Recent Activity */}
      <div className="bg-white rounded-xl border border-gray-200">
        <div className="px-6 py-4 border-b border-gray-100 flex items-center justify-between">
          <h3 className="font-semibold text-gray-900">üîî Recent Activity</h3>
          <button className="text-sm text-blue-600 hover:text-blue-700">View All</button>
        </div>
        <div className="p-4">
          <div className="flex gap-4 overflow-x-auto pb-2">
            {events.slice(0, 5).map((event) => (
              <div key={event.id} className="flex-shrink-0 w-72 bg-gray-50 rounded-lg p-4">
                <div className="flex items-start gap-3">
                  <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 text-sm flex-shrink-0">
                    {event.visitorName.split(' ').map(n => n[0]).join('')}
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="text-sm font-medium text-gray-900 truncate">{event.visitorName}</div>
                    <div className="text-xs text-gray-500">
                      {event.action === 'view' && `Viewed ${event.contentName}`}
                      {event.action === 'download' && `Downloaded ${event.contentName}`}
                      {event.action === 'share' && `Shared ${event.contentName}`}
                      {event.action === 'time_spent' && `Spent ${formatTime(event.duration || 0)}`}
                    </div>
                    <div className="text-xs text-gray-400 mt-1">{event.timestamp}</div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Filters & Rooms List */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
          >
            <option value="">All Status</option>
            <option value="active">Active</option>
            <option value="draft">Draft</option>
            <option value="closed">Closed</option>
            <option value="archived">Archived</option>
          </select>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value as any)}
            className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
          >
            <option value="recent">Most Recent</option>
            <option value="engagement">Highest Engagement</option>
            <option value="value">Deal Value</option>
          </select>
        </div>
        <div className="text-sm text-gray-500">
          {filteredRooms.length} room{filteredRooms.length !== 1 ? 's' : ''}
        </div>
      </div>

      {/* Rooms Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredRooms.map((room) => (
          <div
            key={room.id}
            className="bg-white rounded-xl border border-gray-200 overflow-hidden hover:shadow-lg transition-shadow cursor-pointer"
            onClick={() => onViewRoom(room)}
          >
            {/* Room Header */}
            <div className="h-32 bg-gradient-to-br from-blue-500 to-purple-600 p-4 flex flex-col justify-between">
              <div className="flex items-center justify-between">
                <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(room.status)}`}>
                  {room.status.charAt(0).toUpperCase() + room.status.slice(1)}
                </span>
                <span className="text-white/80 text-sm">{formatCurrency(room.dealValue)}</span>
              </div>
              <div>
                <h4 className="font-semibold text-white truncate">{room.name}</h4>
                <p className="text-white/80 text-sm">{room.company}</p>
              </div>
            </div>

            {/* Room Stats */}
            <div className="p-4">
              <div className="grid grid-cols-3 gap-4 mb-4">
                <div className="text-center">
                  <div className="text-lg font-bold text-gray-900">{room.totalViews}</div>
                  <div className="text-xs text-gray-500">Views</div>
                </div>
                <div className="text-center">
                  <div className="text-lg font-bold text-gray-900">{room.uniqueVisitors}</div>
                  <div className="text-xs text-gray-500">Visitors</div>
                </div>
                <div className="text-center">
                  <div className={`text-lg font-bold ${getEngagementColor(room.engagementScore)}`}>
                    {room.engagementScore}%
                  </div>
                  <div className="text-xs text-gray-500">Engagement</div>
                </div>
              </div>

              {/* Engagement Bar */}
              <div className="mb-4">
                <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
                  <div
                    className={`h-full rounded-full ${
                      room.engagementScore >= 80 ? 'bg-emerald-500' :
                      room.engagementScore >= 50 ? 'bg-amber-500' : 'bg-red-500'
                    }`}
                    style={{ width: `${room.engagementScore}%` }}
                  />
                </div>
              </div>

              {/* Stakeholders */}
              {room.stakeholders.length > 0 && (
                <div className="flex items-center justify-between mb-4">
                  <div className="flex -space-x-2">
                    {room.stakeholders.slice(0, 4).map((s) => (
                      <div
                        key={s.id}
                        className={`w-8 h-8 rounded-full flex items-center justify-center text-xs font-medium border-2 border-white ${
                          s.isChampion ? 'bg-amber-100 text-amber-700' : 'bg-blue-100 text-blue-700'
                        }`}
                        title={`${s.name}${s.isChampion ? ' (Champion)' : ''}`}
                      >
                        {s.name.split(' ').map(n => n[0]).join('')}
                      </div>
                    ))}
                    {room.stakeholders.length > 4 && (
                      <div className="w-8 h-8 rounded-full bg-gray-100 flex items-center justify-center text-xs font-medium border-2 border-white">
                        +{room.stakeholders.length - 4}
                      </div>
                    )}
                  </div>
                  <span className="text-xs text-gray-500">{room.contentItems} items</span>
                </div>
              )}

              {/* Last Visited */}
              <div className="text-xs text-gray-400" suppressHydrationWarning>
                {room.lastVisited ? (
                  <>Last visited: {formatDateSafe(room.lastVisited, room.lastVisited)}</>
                ) : (
                  <>Created: {formatDateSafe(room.createdAt, room.createdAt)}</>
                )}
              </div>
            </div>
          </div>
        ))}

        {/* Create Room Card */}
        <div
          className="bg-gray-50 rounded-xl border-2 border-dashed border-gray-300 p-8 flex flex-col items-center justify-center cursor-pointer hover:border-blue-400 hover:bg-blue-50 transition-colors min-h-[280px]"
          onClick={onCreateRoom}
        >
          <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 text-2xl mb-4">
            +
          </div>
          <h4 className="font-medium text-gray-900 mb-1">Create New Room</h4>
          <p className="text-sm text-gray-500 text-center">
            Start a personalized buyer experience
          </p>
        </div>
      </div>
    </div>
  )
}

// ==============================================
// CONTENT LIBRARY TAB
// ==============================================
function ContentLibraryTab({ content }: { content: ContentItem[] }) {
  const [searchQuery, setSearchQuery] = useState('')
  const [typeFilter, setTypeFilter] = useState('')
  const [categoryFilter, setCategoryFilter] = useState('')

  const categories = useMemo(() => {
    return [...new Set(content.map(c => c.category))]
  }, [content])

  const filteredContent = useMemo(() => {
    return content.filter(item => {
      if (searchQuery && !item.name.toLowerCase().includes(searchQuery.toLowerCase())) return false
      if (typeFilter && item.type !== typeFilter) return false
      if (categoryFilter && item.category !== categoryFilter) return false
      return true
    })
  }, [content, searchQuery, typeFilter, categoryFilter])

  return (
    <div className="space-y-6">
      {/* Actions Bar */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="relative">
            <input
              type="search"
              placeholder="Search content..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg text-sm w-64"
            />
            <svg className="w-4 h-4 absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
          <select
            value={typeFilter}
            onChange={(e) => setTypeFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
          >
            <option value="">All Types</option>
            <option value="document">Documents</option>
            <option value="video">Videos</option>
            <option value="presentation">Presentations</option>
            <option value="link">Links</option>
            <option value="proposal">Proposals</option>
          </select>
          <select
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
          >
            <option value="">All Categories</option>
            {categories.map(cat => (
              <option key={cat} value={cat}>{cat}</option>
            ))}
          </select>
        </div>
        <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm flex items-center gap-2">
          <span>‚¨ÜÔ∏è</span>
          Upload Content
        </button>
      </div>

      {/* Content Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
        {filteredContent.map((item) => (
          <div
            key={item.id}
            className="bg-white rounded-xl border border-gray-200 overflow-hidden hover:shadow-lg transition-shadow"
          >
            {/* Thumbnail */}
            <div className="h-32 bg-gradient-to-br from-gray-100 to-gray-200 flex items-center justify-center">
              <span className="text-5xl">{getContentIcon(item.type)}</span>
            </div>

            {/* Content Info */}
            <div className="p-4">
              <div className="flex items-start justify-between mb-2">
                <h4 className="font-medium text-gray-900 text-sm truncate flex-1">{item.name}</h4>
              </div>
              <div className="flex items-center gap-2 mb-3">
                <span className="px-2 py-0.5 text-xs bg-gray-100 text-gray-600 rounded">{item.type}</span>
                <span className="px-2 py-0.5 text-xs bg-blue-100 text-blue-600 rounded">{item.category}</span>
              </div>
              <div className="grid grid-cols-3 gap-2 text-center text-xs">
                <div>
                  <div className="font-semibold text-gray-900">{item.views}</div>
                  <div className="text-gray-500">Views</div>
                </div>
                <div>
                  <div className="font-semibold text-gray-900">{formatTime(item.avgViewTime)}</div>
                  <div className="text-gray-500">Avg. Time</div>
                </div>
                <div>
                  <div className="font-semibold text-gray-900">{item.downloads}</div>
                  <div className="text-gray-500">Downloads</div>
                </div>
              </div>
              {item.fileSize && (
                <div className="text-xs text-gray-400 mt-3">{item.fileSize}</div>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

