// ==============================================
// ANALYTICS TAB
// ==============================================
function AnalyticsTab({ rooms, events }: { rooms: SalesRoom[]; events: EngagementEvent[] }) {
  const analyticsData = useMemo(() => {
    const totalViews = rooms.reduce((sum, r) => sum + r.totalViews, 0)
    const totalVisitors = rooms.reduce((sum, r) => sum + r.uniqueVisitors, 0)
    const totalTimeSpent = rooms.reduce((sum, r) => sum + (r.avgTimeSpent * r.totalViews), 0)
    const avgEngagement = Math.round(rooms.filter(r => r.engagementScore > 0).reduce((sum, r) => sum + r.engagementScore, 0) / rooms.filter(r => r.engagementScore > 0).length)
    
    // Top performing rooms
    const topRooms = [...rooms].sort((a, b) => b.engagementScore - a.engagementScore).slice(0, 5)
    
    // Action breakdown
    const actionCounts = events.reduce((acc, e) => {
      acc[e.action] = (acc[e.action] || 0) + 1
      return acc
    }, {} as Record<string, number>)
    
    return { totalViews, totalVisitors, totalTimeSpent, avgEngagement, topRooms, actionCounts }
  }, [rooms, events])

  return (
    <div className="space-y-6">
      {/* Overview Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üëÅÔ∏è</div>
          <div className="text-3xl font-bold text-gray-900">{analyticsData.totalViews}</div>
          <div className="text-sm text-gray-500">Total Page Views</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üë 18% vs last month</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üë•</div>
          <div className="text-3xl font-bold text-gray-900">{analyticsData.totalVisitors}</div>
          <div className="text-sm text-gray-500">Unique Visitors</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üë 12% vs last month</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">‚è±Ô∏è</div>
          <div className="text-3xl font-bold text-gray-900">{formatTime(Math.round(analyticsData.totalTimeSpent / analyticsData.totalViews))}</div>
          <div className="text-sm text-gray-500">Avg. Time per Visit</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üë 8% vs last month</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üìà</div>
          <div className={`text-3xl font-bold ${getEngagementColor(analyticsData.avgEngagement)}`}>
            {analyticsData.avgEngagement}%
          </div>
          <div className="text-sm text-gray-500">Avg. Engagement Score</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üë 5% vs last month</div>
        </div>
      </div>

      {/* Two Column Layout */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Top Performing Rooms */}
        <div className="bg-white rounded-xl border border-gray-200">
          <div className="px-6 py-4 border-b border-gray-100">
            <h3 className="font-semibold text-gray-900">üèÜ Top Performing Rooms</h3>
          </div>
          <div className="divide-y divide-gray-100">
            {analyticsData.topRooms.map((room, idx) => (
              <div key={room.id} className="px-6 py-4 flex items-center gap-4">
                <div className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-bold ${
                  idx === 0 ? 'bg-amber-100 text-amber-600' :
                  idx === 1 ? 'bg-gray-200 text-gray-600' :
                  idx === 2 ? 'bg-orange-100 text-orange-600' :
                  'bg-gray-100 text-gray-500'
                }`}>
                  {idx + 1}
                </div>
                <div className="flex-1">
                  <div className="font-medium text-gray-900">{room.name}</div>
                  <div className="text-sm text-gray-500">{room.company}</div>
                </div>
                <div className="text-right">
                  <div className={`font-bold ${getEngagementColor(room.engagementScore)}`}>
                    {room.engagementScore}%
                  </div>
                  <div className="text-xs text-gray-500">{room.totalViews} views</div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Engagement by Action */}
        <div className="bg-white rounded-xl border border-gray-200">
          <div className="px-6 py-4 border-b border-gray-100">
            <h3 className="font-semibold text-gray-900">üìä Engagement by Action</h3>
          </div>
          <div className="p-6 space-y-4">
            {Object.entries(analyticsData.actionCounts).map(([action, count]) => (
              <div key={action}>
                <div className="flex items-center justify-between mb-1">
                  <span className="text-sm text-gray-700 capitalize">{action.replace('_', ' ')}</span>
                  <span className="text-sm font-medium">{count as number}</span>
                </div>
                <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
                  <div
                    className="h-full bg-blue-500 rounded-full"
                    style={{ width: `${((count as number) / events.length) * 100}%` }}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Recent Activity Table */}
      <div className="bg-white rounded-xl border border-gray-200">
        <div className="px-6 py-4 border-b border-gray-100">
          <h3 className="font-semibold text-gray-900">üïê Recent Activity</h3>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-200">
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Visitor</th>
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Room</th>
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Action</th>
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Content</th>
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Time</th>
              </tr>
            </thead>
            <tbody>
              {events.map((event) => (
                <tr key={event.id} className="border-b border-gray-100 hover:bg-gray-50">
                  <td className="py-3 px-6">
                    <div className="flex items-center gap-2">
                      <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 text-xs font-medium">
                        {event.visitorName.split(' ').map(n => n[0]).join('')}
                      </div>
                      <div>
                        <div className="text-sm font-medium text-gray-900">{event.visitorName}</div>
                        <div className="text-xs text-gray-500">{event.visitorEmail}</div>
                      </div>
                    </div>
                  </td>
                  <td className="py-3 px-6 text-sm text-gray-600">{event.roomName}</td>
                  <td className="py-3 px-6">
                    <span className={`px-2 py-1 text-xs rounded-full font-medium ${
                      event.action === 'view' ? 'bg-blue-100 text-blue-700' :
                      event.action === 'download' ? 'bg-emerald-100 text-emerald-700' :
                      event.action === 'share' ? 'bg-purple-100 text-purple-700' :
                      'bg-gray-100 text-gray-700'
                    }`}>
                      {event.action}
                    </span>
                  </td>
                  <td className="py-3 px-6 text-sm text-gray-600">{event.contentName || '-'}</td>
                  <td className="py-3 px-6 text-sm text-gray-500">{event.timestamp}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

